// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueLazyload from 'vue-lazyload' //vue

import videoCss from 'video.js/dist/video-js.min.css' //
require('video.js'); //

import setWechatTitle from './utils/setWechatTitle.js' //

import weui from 'weui'; //weui css 

// require('weixin-js-sdk'); //wxJSSDK


Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: '',
  loading: 'http://localhost:7777/static/images/loading.gif',
  attempt: 1
});


//
router.beforeEach((to, from, next) => {

   setWechatTitle(to.meta.title);

  next()
})
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



